<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }" @click.native="clearStorage"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="openSetRole"></el-button>
        </el-col>
      </el-row>
      <el-table
        :data="rolesList"
        border
        style="width: 100%">
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
            class="rolesRow"
            :class="{rolesBoderBottom: id1 === 0}"
            v-for="(item1, id1) in scope.row.children"
            :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightsById(scope.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row class="rolesRow"
                :class="{rolesBoderBottom: id2 === 0}"
                v-for="(item2, id2) in item1.children"
                :key="item2.id"
                >
                <!--  -->
                  <el-col :span="6">
                    <el-tag closable type="success"
                    @close="removeRightsById(scope.row, item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag closable type="warning"
                    v-for="(item3) in item2.children"
                    :key="item3.id"
                    @close="removeRightsById(scope.row, item3.id)"
                    >
                    {{item3.authName}}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="roleName" label=""></el-table-column>
        <el-table-column prop="roleDesc" label=""></el-table-column>
        <el-table-column label="" width="300">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" icon="el-icon-edit" @click="openRoleInfo(scope.row)"></el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete" @click="deleteRole(scope.row)"></el-button>
            <el-button type="warning" size="mini" icon="el-icon-share" @click="setRoleRights(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="roleVisible"
        width="50%"
        @close="dialogClose"
        >
        <!--  -->
        <el-tree
        :data="rightsList"
        :props="defaultProps"
        show-checkbox
        default-expand-all
        highlight-current
        node-key="id"
        :default-checked-keys="checkKeys"
        ref="treeRefs"
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="roleVisible = false"> </el-button>
          <el-button type="primary" @click="addRights"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="setRoleVisible"
        width="50%"
        @close="resetRoleForm"
        >
        <el-form
        label-width="80px"
        :model="roleForm"
        :rules="roleFormRules"
        ref="roleRefs"
        >
          <el-form-item label="" prop="roleName">
            <el-input v-model="roleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="roleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRoleVisible = false"> </el-button>
          <el-button type="primary" @click="saveRoleInfo"></el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editRoleVisible"
        width="50%"
        >
        <el-form
        label-width="80px"
        :model="editRoleForm"
        :rules="roleFormRules"
        ref="role"
        >
          <el-form-item label="" prop="roleName">
            <el-input v-model="editRoleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="editRoleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRoleVisible = false"> </el-button>
          <el-button type="primary" @click="editRoleInfo"></el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rolesList: [],
      roleVisible: false,
      setRoleVisible: false,
      editRoleVisible: false,
      rightsList: [],
      defaultProps: {
        children: 'children',
        label: 'authName'
      },
      checkKeys: [],
      roleId: '',
      roleForm: {
        roleName: '',
        roleDesc: ''
      },
      editRoleForm: {},
      roleFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getRoleList()
  },
  methods: {
    clearStorage () {
      window.sessionStorage.setItem('activePath', '')
      this.$router.go(0)
    },
    async getRoleList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('')
      this.rolesList = res.data
    },
    removeRightsById (role, rightId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
        if (res.meta.status !== 200) return this.$message.error('')
        // this.getRoleList()
        role.children = res.data
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    async setRoleRights (role) {
      // 
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error('')
      this.rightsList = res.data
      this.getLastKeys(role, this.checkKeys)
      this.roleVisible = true
    },
    getLastKeys (node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach((item) => {
        this.getLastKeys(item, arr)
      })
    },
    dialogClose () {
      this.checkKeys = []
    },
    async addRights () {
      const keys = [
        ...this.$refs.treeRefs.getCheckedKeys(),
        ...this.$refs.treeRefs.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getRoleList()
      this.roleVisible = false
    },
    openSetRole () {
      this.setRoleVisible = true
    },
    saveRoleInfo () {
      this.$refs.roleRefs.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.post('roles', this.roleForm)
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        this.getRoleList()
      })
    },
    resetRoleForm () {
      this.$refs.roleRefs.resetFields()
    },
    openRoleInfo (row) {
      this.editRoleVisible = true
      this.editRoleForm = row
    },
    editRoleInfo () {
      this.$refs.role.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.put('roles/' + this.editRoleForm.id, {
          id: this.editRoleForm.id,
          roleName: this.editRoleForm.roleName,
          roleDesc: this.editRoleForm.roleDesc
        })
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        this.getRoleList()
        this.editRoleVisible = false
      })
    },
    deleteRole (row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error'
      }).then(async () => {
        const { data: res } = await this.$http.delete('roles/' + row.id)
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message({
          type: 'success',
          message: '!'
        })
        this.getRoleList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style lang="less" scope>
.rolesRow {
  padding: 10px;
  border-top: 1px solid #ccc;
  display: flex;
  align-items: center;
}
.rolesBoderBottom {
  border-top: none;
}
.el-tag {
  margin: 5px;
}
</style>
